.page-banner {
  position: relative;
  height: 412px;
  margin-top: 104px;
}

.page-banner:before {
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(19, 58, 88, 0.5) 0%, rgba(32, 104, 132, 0) 99.99%);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
}

.page-banner_image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.page-titles {
  height: 412px;
  max-width: 470px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  position: relative;
  z-index: 10;
}

.page-titles h1 {
  font-size: 48px;
  line-height: 57px;
  margin-bottom: 15px;
  color: #fff;
}

.page-titles p {
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
}

@media screen and (max-width: 500px) {
  .page-banner {
    height: 365px;
    margin-top: 79px;
  }
  .page-titles {
    height: 365px;
  }
  .page-titles h1 {
    font-size: 44px;
    line-height: 52px;
  }
  .page-titles p {
    font-size: 21px;
    line-height: 30px;
  }
}

